.wrapper {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  padding-top: 180px;

  .container {
    position: relative;
    box-sizing: border-box;
    max-width: 1200px;
    padding: 0 20px;
    width: 100%;
    margin: 0 auto;

    .topRow {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 0 0 10px;
      margin-bottom: 60px;

      h2.title {
        //display: inline-block;
        width: 480px;

        span {
          text-transform: uppercase;
          display: block;
          line-height: 1.6;
        }

        span:first-of-type {
          text-transform: none;
          font-family: "Helvetica Neue Medium";
          font-size: 23px;
          position: relative;
          width: fit-content;

          &:after {
            content: "";
            display: block;
            position: absolute;
            width: 60px;
            height: 1px;
            background-color: #171717;
            top: 45%;
            right: -70px;
          }
        }

        span:last-of-type {
          font-family: "Helvetica Neue Black";
          font-size: 45px;
          line-height: 1.1;
        }

        @media (max-width: 500px) {
          width: fit-content;

          span:first-of-type {
            font-size: 18px;
          }
          span:last-of-type {
            font-size: 11.2vw;
          }
        }

        @media (max-width: 1024px) {
          margin-bottom: 20px;
        }

      }

      .textContent {
        padding: 0 20px;
        text-align: justify;
        width: 60%;
        position: relative;

        .btn {
          margin-top: 15px;
          text-decoration: underline;
          cursor: pointer;
        }

        .description {
          z-index: -1;
          opacity: 0;
          max-width: 625px;
          width: calc(100%);
          position: absolute;
          box-sizing: border-box;
          padding: 30px;
          bottom: -20px;
          transform: translateY(100%);
          box-shadow: 0px 4px 27px 0px rgba(23, 23, 23, 0.52);
          background-color: rgb(246, 246, 246);
          transition: opacity 200ms;

          p {
            margin-bottom: 20px;
          }

          ul {
            li{list-style-type:"â€“  ";}
          }

          &.active {
            z-index: 1;
            opacity: 1;
          }
          img {
            width: 68px;
            margin-bottom: 15px;
          }
        }

      }

      @media (max-width: 1024px) {
        display: block;
        //padding: 0 20px;
        .textContent {
          box-sizing: border-box;
          width: 100%;
          padding: 0;
        }
        h4.title {
          display: inline-block;
          width: fit-content;
          //padding-left: 20px;
        }
      }

      @media (max-width: 500px) {
        padding: 0;
        display: block;
      }

    }

    .links {
      ul {
        list-style: none;
        li {
          position: relative;
          padding-left: 18px;
          margin-bottom: 14px;
          text-decoration: underline;
          a {font-family: "Helvetica Neue Medium", sans-serif;}
          &:before {
            content: "";
            width: 13px;
            height: 3px;
            background-color: #ff6c39;
            position: absolute;
            top: 6px;
            left: -8px;
            @media (max-width: 768px) {
              top: 5px;
              height: 2px;
            }
          }
        }
      }
    }
  }

  @media (max-width: 1024px) {
    padding-top: 100px;
  }

}
